import React from 'react';
import { func, object } from 'prop-types';
import Link from 'gatsby-link';
import Helmet from 'react-helmet';
import 'normalize.css';

import Header from './Header/Header';
import Footer from './Footer/Footer';
import '../styles/styles.scss';

import './index.scss';

class TemplateWrapper extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isTop: true,
            intervalId: 0,
            isLanding: false
        };

        this.onScroll = this.onScroll.bind(this);
        this.scrollToTop = this.scrollToTop.bind(this);
        this.scrollStep = this.scrollStep.bind(this);
    }

    componentDidMount() {
        document.addEventListener('scroll', this.onScroll, false);

        this.setState({
            isLanding: window.location.href.includes('/lp/') 
        });
    }

    componentWillUnmount() {
        document.removeEventListener('scroll', this.onScroll, false);
    }

    onScroll() {
        this.setState({ isTop: window.scrollY < 100 })
    }

    scrollStep() {
        if (window.pageYOffset === 0) {
            clearInterval(this.state.intervalId);
        }

        const step = 10;

        window.scroll(0, window.pageYOffset - step);
    }
  
    scrollToTop() {
        const intervalId = setInterval(this.scrollStep, this.props.delayInMs);
        this.setState({ intervalId: intervalId });
    }

    render() {
        const { data, children } = this.props;
        const { isTop, isLanding } = this.state;

        return (
            <div className="app__container">
    
                <Helmet
                    title="SalesHero: Sales AI"
                    meta={[
                        { name: 'description', content: 'AI assistant for sales reps' },
                        { name: 'keywords', content: 'AI sales assistant' },
                    ]} 
                    script={[{
                        type: 'text/javascript',
                        innerHTML: `(function(a,s,y,n,c,h,i,d,e){s.className+=' '+y;h.start=1*new Date;
                            h.end=i=function(){s.className=s.className.replace(RegExp(' ?'+y),'')};
                            (a[n]=a[n]||[]).hide=h;setTimeout(function(){i();h.end=null},c);h.timeout=c;
                            })(window,document.documentElement,'async-hide','dataLayer',4000,
                            {'GTM-T5TGXQJ':true});`
                    }]}
                >
                    <link rel="stylesheet" href="http://fonts.googleapis.com/icon?family=Material+Icons" />
                    <script type="text/javascript" src="./dist/shlogo.min.js"></script>
                    
                </Helmet>
    
                <Header data={data} 
                    scrollToTop={this.scrollToTop}
                    isTop={isTop}
                    withNav={!isLanding} />
    
                <div className="app__content">
                    {children()}
                </div>
    
                <Footer data={data}
                        withNav={!isLanding} />    
            </div>
        );    
    }
}

TemplateWrapper.propTypes = {
    children: func,
    data: object
}

export default TemplateWrapper

export const query = graphql`
    query NavigationQuery {
        allMarkdownRemark {
            edges {
                node {
                    frontmatter {
                        scope
                        title
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`;



